*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.navbar{
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: #16191a;
    font-family: Verdana;
    color: #fff;
    height: 75px;
}
.logo{
    font-size: 30px;
}
.nav-links{
    display: flex;
    justify-content: flex-end;
    list-style: none;
    width: 75%;
}
.home, .about, .contact, .skills, .signup{
    text-decoration: none;
    color: #fff;
    font-size: 20px;
    padding: 10px 20px;
}
.mobile-menu-icon{
    display: none;
}
@media screen and (max-width:820px){
    .logo{
        display: flex;
        position: absolute;
        left: 15px;
        top: 20px;
    }
    .nav-links{
        display: none;
    }
    .nav-links-mobile{
        position: absolute;
        display: block;
        list-style: none;
        background-color: #16191a;
        left: 0;
        top: 75px;
        width: 100%;
    }
    .home, .about, .contact, .skills, .signup{
        text-align: center;
        color: #fff;
        font-size: 20px;
        padding: 32px;
        width: 100%;
    }
    .mobile-menu-icon{
        display: block;
        position: absolute;
        font-size: 30px;
        color: #fff;
        background-color: #16191a;
        border: none;
        top: 20px;
        right: 25px;
    }
}